var cl_functionalityCalculator = (function() {
    var flag = true;

	function makeWorning(elem, massage, max) {
		if (elem.getAttribute('name') === 'percent') elem.value = elem.value.replace(',','.');

		if (elem.value > max) {
			if (flag === true) {
	           makeTooltip(massage, elem);
	           elem.style.outline = '2px solid red';
	           flag = false;
	           return false;
			}
			return false;		
		}
		if (elem.value <= max) {
			if (flag === false) {
			   elem.style.outline = 'none';
			   removeTooltip(elem);
			   flag = true;
			}
		}
	}


	function makeTooltip (str, elem){
		var outPut = elem.parentNode;
		var span = document.createElement('span'); 
		span.className = 'cl-cpanTooltip';
		span.innerHTML = str;
		outPut.appendChild(span);
	}


	function removeTooltip (elem) {
		elem = elem.nextElementSibling;
		if (!Element.prototype.remove) {
		    Element.prototype.remove = function remove() {
		      if (this.parentNode) {
		        this.parentNode.removeChild(this);
		      }
		    };
		  } 
		elem.remove();
	}



	function getChar(event) {
		event = event || window.event;
        if (event.which == null) {
           if (event.keyCode < 32) return null;
           return String.fromCharCode(event.keyCode); // IE
        }

	    if (event.which != 0 && event.charCode != 0) {
	        if (event.which < 32) return null;
	        return String.fromCharCode(event.which); // остальные
	    }

	    return null; // специальная клавиша
	}


    return {
    	makeWorning: makeWorning,
    	getChar: getChar
    };
})();



export {cl_functionalityCalculator};